<template>
  <div>
    <div class="keranjang">
      <Header :updateKeranjang="keranjang" />
    </div>
    <div class="container mt-5">
      <h2>Daftar <strong>Pesanan</strong></h2>
      <div class="row">
        <div class="col">
          <table class="table table-striped mt-4">
            <thead>
              <tr>
                <th scope="col">No.</th>
                <th scope="col">Nama Pesanan</th>
                <th scope="col">Harga</th>
                <th scope="col">Jumlah</th>
                <th scope="col">Keterangan</th>
                <th scope="col">Total Harga</th>
                <th scope="col">Aksi</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(cart, index) in keranjang" :key="cart.id">
                <th>{{ index + 1 }}</th>
                <td>{{ cart.products.nama }}</td>
                <td>Rp. {{ cart.products.harga }}</td>
                <td>{{ cart.jumlah_pesanan }}</td>
                <td>{{ cart.keterangan ? cart.keterangan : "-" }}</td>
                <td>Rp. {{ cart.jumlah_pesanan * cart.products.harga }}</td>
                <td align="left" class="text-danger">
                  <b-icon-trash @click="hapusPesanan(cart.id)"></b-icon-trash>
                </td>
              </tr>

              <tr>
                <td colspan="5" align="right"><strong>Total Harga</strong></td>
                <td>
                  <strong>Rp. {{ totalHarga }}</strong>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <div class="row justify-content-end">
        <div class="col-md-4">
          <form class="mt-3" v-on:submit.prevent>
            <div class="form-group">
              <label for="nama">Nama Pemesan</label>
              <input type="text" class="form-control" v-model="checkout.nama" />
            </div>
            <div class="form-group">
              <label for="nama">Nomor Meja</label>
              <input
                type="number"
                class="form-control"
                v-model="checkout.noMeja"
              />
            </div>
            <button
              type="submit"
              class="btn btn-success btn-sm float-right"
              @click="Checkout"
            >
              Checkout
            </button>
          </form>
        </div>
      </div>
    </div>
  </div> 
</template>

<script>
import Header from "@/components/Header.vue";
import axios from "axios";

export default {
  name: "Keranjang",
  components: {
    Header,
  },
  data() {
    return {
      keranjang: {},
      checkout: {},
    };
  },
  methods: {
    setKeranjang(data) {
      this.keranjang = data;
    },
    hapusPesanan(id) {
      axios
        .delete("http://localhost:3000/keranjangs/" + id)
        .then(() => {
          this.$toast.error("Data Pesanan Berhasil Dihapus!!!", {
            type: "error",
            position: "top",
            duration: 3000,
            dismissible: true,
          });

          // Update dan Reload
          axios
            .get("http://localhost:3000/keranjangs")
            .then((response) => this.setKeranjang(response.data))
            .catch((error) => console.log(error));
        })
        .catch((error) => console.log(error));
    },
    Checkout() {
      if (this.checkout.nama && this.checkout.noMeja) {
        this.checkout.keranjang = this.keranjang;
        axios
          .post("http://localhost:3000/pesanans", this.checkout)
          .then(() => {
            this.keranjang.map(function (item) {
              return axios.delete("http://localhost:3000/keranjangs/" + item.id)
              .catch((error) => console.log(error));
            });
            this.$router.push({ path: "/" });
            this.$toast.success("Berhasil Dipesan", {
              type: "success",
              position: "top",
              duration: 3000,
              dismissible: true,
            });
          })
          .catch((err) => console.log(err));
      } else {
        this.$toast.error("Nama Pemesan dan Nomor Meja Harus Diisi!!!", {
          type: "error",
          position: "top",
          duration: 3000,
          dismissible: true,
        });
      }
    },
  },
  mounted() {
    if (this.$store.state.isAuthenticated) {
    axios
      .get("http://localhost:3000/keranjangs")
      .then((response) => this.setKeranjang(response.data))
      .catch((error) => console.log(error));
    }
    else {
        this.$router.push("/login");
        this.$toast.error("Silahkan Login Kembali", {
          type: "error",
          position: "top",
          duration: 3000,
          dismissible: true,
        });
      }
  },
  computed: {
    totalHarga() {
      return this.keranjang.reduce(function (item, data) {
        return item + data.products.harga * data.jumlah_pesanan;
      }, 0);
    },
  },
};
</script>

